<script>
import Paper, { Title, Content } from '@smui/paper';
import Textfield from '@smui/textfield';
import Button, { Label } from '@smui/button';
import Checkbox from '@smui/checkbox';
import FormField from '@smui/form-field';
import { onMount } from 'svelte';

let service = window['services'];
let todoService = (service)? service.todoManagerService : undefined;

let todo = '';
let todos = (localStorage.getItem("todos"))? JSON.parse(localStorage.getItem("todos")) : [];
let todoId = 0;

function add() {
    todos.push({
    id: todoId,
    todo: todo,
    completed: false
    });
    todo = "";
    todoId++;
    todos = todos;
    localStorage.setItem("todos", JSON.stringify(todos));
}

function remove(name) {
    if(todoService != undefined) todoService.deletedObservable().next(name);
    for(var i = 0; i<todos.length; i++) {
        if(todos[i].todo == name) {
            todos.splice(i, 1);
        }
    }
    todos = todos;
    localStorage.setItem("todos", JSON.stringify(todos));
}

function complete(name) {
    for(var i = 0; i<todos.length; i++) {
        if(todos[i].todo == name) {
            todos[i].completed = !todos[i].completed;
        }
    }
    todos = todos;
    localStorage.setItem("todos", JSON.stringify(todos));
}

onMount(() => {
    if(todoService != undefined){
        var addObserver = todoService.addedObservable().subscribe((data)=>{
            todo = data;
            add();
        });
    }
});
</script>

<svelte:options tag="todo-svelte" />
<main>
    <Paper color="primary">
        <Title>To-do list</Title>
        <Content>
            <div class="form">
                <div>
                    <Textfield variant="outlined" bind:value={todo} label="Write the task">
                    </Textfield>
                </div>
                <Button color="primary" on:click={() => add()} variant="raised">
                    <Label>Add</Label>
                </Button>
            </div>
            {#each todos as t}
            <div class="todo-wrapper completed-{t.completed}">
                <Paper>
                    <div class="todo-content">
                        <div class="todo-title">
                            <FormField>
                                <Checkbox checked={t.completed} on:click={() => complete(t.todo)}/>
                            </FormField>
                            <p>{t.todo}</p>
                        </div>
                        <Button color="primary" on:click={() => remove(t.todo)} variant="raised">
                            <Label>Delete</Label>
                        </Button>
                    </div>
                </Paper>
            </div>    
            {/each}
        </Content>
    </Paper>
</main>

<style scoped>
@import url('https://cdn.jsdelivr.net/npm/svelte-material-ui/bare.min.css');

main{
	margin: auto;
    width: fit-content;
    text-align: center;
    --mdc-theme-primary: var(--theme-primary, #6585c2);
	--mdc-theme-on-primary: var(--theme-on-primary, #f2f2f2);
	--mdc-theme-secondary: var(--theme-secondary, #eb9486);
	--mdc-theme-on-secondary: var(--theme-on-secondary, #000);
}

.form{
    display: flex;
    padding: 1rem;
    gap: 2rem;
    align-items: center;
    justify-content: space-between;
    --mdc-theme-primary: var(--theme-secondary, #eb9486);
    --mdc-theme-on-primary: var(--theme-on-secondary, #000);
}

.todo-wrapper{
    margin-top:2rem;
}

.completed-true{
    --mdc-theme-surface: rgb(172, 172, 172);
}

.todo-content{
    display: flex;
    width: 100%;
    align-items: center;
    justify-content: space-between;
    --mdc-theme-primary: var(--theme-secondary, #eb9486);
    --mdc-theme-on-primary: var(--theme-on-secondary, #000);
}

.todo-title{
    display: flex;
    align-items: center;
}

p{
    font-size:1.25rem;
    margin:0px;
}
</style>